import React from 'react'
import {
  HandIcon,
  MoneyIcon,
  PersonIcon,
  SchoolIcon,
} from '../../icons'
import { scrollTo } from '../../../utils'

import './modalities_section.scss'

export default ({ t }) => {
  const items = [
    {
      title: 'modalities.m1.title',
      description: 'modalities.m1.description',
      button: 'modalities.m1.button',
      link: 'applyScholar',
      iconClass: '--school',
      Icon: SchoolIcon,
    },
    {
      title: 'modalities.m2.title',
      description: 'modalities.m2.description',
      button: 'modalities.m2.button',
      link: 'contact',
      iconClass: '--hand',
      Icon: HandIcon,
    },
    {
      title: 'modalities.m3.title',
      description: 'modalities.m3.description',
      button: 'modalities.m3.button',
      link: 'contact',
      iconClass: '--money',
      Icon: MoneyIcon,
    },
    {
      title: 'modalities.m4.title',
      description: 'modalities.m4.description',
      button: 'modalities.m4.button',
      link: 'contact',
      iconClass: '--person',
      Icon: PersonIcon,
    },
  ]

  const itemComponetns = items.map(({
    title,
    description,
    button,
    link,
    iconClass,
    Icon,
  }) => (
    <div className="home-mds__item" key={title}>
      <div className={`home-mds__item--icon-wrapper ${iconClass}`}>
        <span className="home-mds__item--icon">
          <Icon size={48} fill="#144fb8" />
        </span>
      </div>
      <h2 className="home-mds__item--title">
        {t(title)}
        <hr className="home-mds__item--line" />
      </h2>
      <p className="home-mds__item--description">
        {t(description)}
      </p>
      <div className="home-mds__item--button-wrapper">
        <button
          onClick={() => scrollTo(link)}
          className="home-mds__item--button button --primary"
        >
          {t(button)}
        </button>
      </div>
    </div>
  ))

  return (
    <div className="home-mds" id="apply">
      <div className="home-mds__wrapper">
        <h1 className="home-mds__title">
          {t('modalities.title')}
        </h1>
        <div className="home-mds__content-wrapper">
          {itemComponetns}
        </div>
      </div>
    </div>
  )
}



