import React from 'react'
import { LogoPerson } from '../../common'

import './about_section.scss'

export default ({ t }) => (
  <div className="home-as" id="about">
    <div className="home-as__wrapper">
      <div className="home-as__image">
        <LogoPerson />
      </div>
      <div className="home-as__content-wrapper">
        <h1 className="home-as__title">
          {t('about.title')}
        </h1>
        <p className="home-as__description">
          {t('about.description.part1')}
        </p>
        <p className="home-as__description">
          {t('about.description.part2')}
        </p>
      </div>
    </div>
  </div>
)



