import React, { Component } from 'react'
import { withRouter } from 'react-router'

const HomeHOCWrapper = InnerComponent => {
  class HomeHOC extends Component {
    state = {
      modules: [],
    }

    componentDidMount () {
      Promise.all([
        import('./scholarship_section/scholarship_section'),
        import('./modalities_section/modalities_section'),
        import('./apply_scholar_section/apply_scholar_section'),
        import('./sponsorships_section/sponsorships_section'),
        import('./contributors_section/contributors_section'),
        import('./contact_section/contact_section'),
      ]).then(modules => {
        this.setState({ modules: modules.map(m => m.default) })
      })
    }

    render () {
      const { modules } = this.state
      const sections = modules.map((Module, index) => <Module {...this.props} key={index} />)

      return (
        <InnerComponent
          {...this.state}
          {...this.props}
          sections={sections}
        />
      )
    }
  }

  return withRouter(HomeHOC)
}

export default HomeHOCWrapper



