import React from 'react'
// import { StairsLoader } from '../../common'

import './apply_scholar_section.scss'

export default ({ t }) => (
  <div className="home-ass" id="applyScholar">
    <div className="home-ass__wrapper">
      <h1 className="home-ass__title">
        {t('scholar.title')}
      </h1>
      <p className="home-ass__description">
        {t('scholar.description')}
      </p>
      <div className="home-ass__process">
        <p className="home-ass__process--text">
          {t('scholar.selection.text')}
          <span className="home-ass__process--status">
            {t('scholar.selection.status')}
          </span>
        </p>
        <p className="home-ass__process--result">
          {t('scholar.selection.result')}
        </p>
        <ul className="home-ass__process--result-classified">
          <li>Erica Bianca Bravim</li>
          <li>Isaque Castelo Coutinho</li>
          <li>Yan Victor Ribeiro Marim</li>
        </ul>
        <p className="home-ass__process--facebook">
          {t('scholar.selection.facebook.text')}
          <a
            className="home-ass__process--facebook-link"
            href="https://www.facebook.com/institutoelevar/"
            target="_blank"
            rel="noopener noreferrer"
          >
            {t('scholar.selection.facebook.link')}
          </a>
        </p>
      </div>
    </div>
  </div>
)



