import React from 'react'
import { scrollTo } from '../../../utils'

import './sponsorships_section.scss'
import stairsImage from '../../../assets/stairs.png'

export default ({ t }) => (
  <div
    id="sponsors"
    className="home-sps"
    style={{ backgroundImage: `url("${stairsImage}")` }}
  >
    <div className="home-sps__wrapper">
      <p className="home-sps__description">
        {t('sponsorships.description')}
      </p>
      <button
        onClick={() => scrollTo('contact')}
        className="home-sps__button button --primary"
      >
        {t('sponsorships.button')}
      </button>
    </div>
  </div>
)



