import React from 'react'
import { withSiteData } from 'react-static'
import { translate } from 'react-i18next'
import HomeHOC from './home_hoc'
import MainSection from './main_section/main_section'
import AboutSection from './about_section/about_section'

const Home = withSiteData(({ sections, ...props }) => (
  <div className="home">
    <MainSection {...props} />
    <AboutSection {...props} />
    {sections}
  </div>
))

export default translate('home')(HomeHOC(Home))



