import React from 'react'
import {
  FacebookIcon,
  LinkedInIcon,
} from '../../icons'

import './contributors_section.scss'
import andreImage from '../../../assets/contributors/andre.jpg'
import eduardaImage from '../../../assets/contributors/eduarda.jpg'
import lucasBassettiImage from '../../../assets/contributors/lucas_bassetti.jpg'
import lucasImage from '../../../assets/contributors/lucas.jpg'
import rachelImage from '../../../assets/contributors/rachel.jpg'
import tallesImage from '../../../assets/contributors/talles.jpg'
import thaisImage from '../../../assets/contributors/thais.jpg'

export default ({ t }) => {
  const contributors = [
    {
      name: 'Talles Souza',
      position: 'contributors.positions.coFounder',
      image: tallesImage,
      links: {
        facebook: 'https://www.facebook.com/tallestms',
        linkedin: 'https://www.linkedin.com/in/tallestms/',
      },
    },
    {
      name: 'Thas Souza',
      position: 'contributors.positions.coFounder2',
      image: thaisImage,
      links: {
        facebook: 'https://www.facebook.com/thais.tatagiba',
        linkedin: 'https://www.linkedin.com/in/thaistatagiba/',
      },
    },
    {
      name: 'Lucas Raggi',
      position: 'contributors.positions.legalAdivisor',
      image: lucasImage,
      links: {
        linkedin: 'https://www.linkedin.com/in/lucas-raggi-tatagiba-cordeiro-a0713410a',
      },
    },
    {
      name: 'Rachel Boerboon',
      position: 'contributors.positions.volunteer2',
      image: rachelImage,
      links: {},
    },
    {
      name: 'Eduarda Lira',
      position: 'contributors.positions.volunteer2',
      image: eduardaImage,
      links: {},
    },
    {
      name: 'Andre Passamani',
      position: 'contributors.positions.volunteer',
      image: andreImage,
      links: {
        linkedin: 'https://www.linkedin.com/in/andre-passamani-castelo-branco-51213867/',
      },
    },
    {
      name: 'Lucas Bassetti',
      position: 'contributors.positions.volunteer',
      image: lucasBassettiImage,
      links: {
        facebook: 'https://www.facebook.com/lucasbr.dafonseca',
        linkedin: 'https://www.linkedin.com/in/lucas-bassetti-67909836/',
      },
    },
  ]

  const contributorComponents = contributors.map(({
    name,
    position,
    image,
    links,
  }) => (
    <div key={name} className="home-cs__contributor">
      <div
        className="home-cs__contributor--image-wrapper"
        style={{ backgroundImage: `url("${image}")` }}
        title={name}
      >
        {
          Object.keys(links).length > 0 && (
            <div className="home-cs__contributor--links">
              {
                links.facebook && (
                  <a
                    className="home-cs__contributor--link"
                    href={links.facebook}
                    target="_blank"
                  >
                    <FacebookIcon size={24} fill="#ffffff" />
                  </a>
                )
              }
              {
                links.linkedin && (
                  <a
                    className="home-cs__contributor--link"
                    href={links.linkedin}
                    target="_blank"
                  >
                    <LinkedInIcon size={24} fill="#ffffff" />
                  </a>
                )
              }
            </div>
          )
        }
      </div>
      <div className="home-cs__contributor--details">
        <span className="home-cs__contributor--name">
          {name}
        </span>
        <span className="home-cs__contributor--position">
          {t(position)}
        </span>
      </div>
    </div>
  ))

  return (
    <div className="home-cs" id="contributors">
      <div className="home-cs__wrapper">
        <h1 className="home-cs__title">
          {t('contributors.title')}
        </h1>
        <div className="home-cs__contributors">
          {contributorComponents}
        </div>
      </div>
    </div>
  )
}



