import React from 'react'
import { translate } from 'react-i18next'
import HeaderHOC from './header_hoc'
import { LogoShort } from '../../components/logo'
import {
  MenuIcon,
  CloseIcon,
  BrazilIcon,
  USAIcon,
} from '../../components/icons'

import './header.scss'

const Header = ({
  t,
  language,
  opened,
  onToggleMenu,
  onAnchorClick,
  onChangeLanguage,
}) => (
  <nav className="header" id="header">
    <div className="header__wrapper">
      <div className="header__logo">
        <button onClick={() => onAnchorClick('header')}>
          <LogoShort />
        </button>
      </div>
      <div className="header__mobile">
        <button
          onClick={() => onToggleMenu()}
        >
          <MenuIcon fill="#ffffff" size={36} />
        </button>
      </div>
      <div className={`header__links ${opened ? '--opened' : ''}`}>
        <div className="header__links--close-menu">
          <button
            className="header__links--close-button"
            onClick={() => onToggleMenu()}
          >
            <CloseIcon fill="#144fb8" size={36} />
          </button>
        </div>
        <a
          className="header__link"
          onClick={() => onAnchorClick('about')}
        >
          {t('about')}
        </a>
        <a
          className="header__link"
          onClick={() => onAnchorClick('apply')}
        >
          {t('apply')}
        </a>
        <a
          className="header__link"
          onClick={() => onAnchorClick('sponsors')}
        >
          {t('sponsors')}
        </a>
        <a
          className="header__link"
          onClick={() => onAnchorClick('contributors')}
        >
          {t('contributors')}
        </a>
        <a
          className="header__link --contact"
          onClick={() => onAnchorClick('contact')}
        >
          {t('contact')}
        </a>
        <a
          className="header__language"
          onClick={() => onChangeLanguage(language === 'pt-BR' ? 'en' : 'pt-BR')}
        >
          {
            language === 'pt-BR' ? (
              <USAIcon
                height={12}
                width={16}
              />
            ) : (
              <BrazilIcon
                height={12}
                width={16}
              />
            )
          }
        </a>
      </div>
    </div>
  </nav>
)

export default translate('header')(HeaderHOC(Header))



