import React from 'react'
// import ContactHOC from './contact_section_hoc'

import './contact_section.scss'

const ContactSection = ({ t }) => (
  <div className="home-cts" id="contact">
    <div className="home-cts__wrapper">
      <h1 className="home-cts__title">
        {t('contact.title')}
      </h1>
      <p className="home-cts__contact">
        {t('contact.description')}
        <a
          className="home-cts__contact--email"
          href="mailto:contato@institutoelevar.com.br"
        >
          contato@institutoelevar.com.br
        </a>
      </p>
    </div>
  </div>
)

export default ContactSection



